.pop-up-background {

  position: fixed;
  z-index: 10;
  background-color: rgba(#000000, 0.8);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-inline: rem(20);
  // display: none;
}

.popUp-info {
  position: relative;
  background-color: $white;
  padding: rem(20);
  border-radius: rem(20);
  max-width: rem(600);

  @include tablet {
    border-radius: rem(5);
    padding: rem(40);
  }

  &__form {
    display: grid;
  }

  &__title {
    font-size: rem(21);
    text-align: center;
    font-weight: 700;
    margin-block-end: rem(20);

    @include tablet {
      margin-block-end: rem(35);
      font-size: rem(30);
    }
  }

  &__description {
    display: none;

    @include tablet {
      display: initial;
      text-align: center;
      font-style: italic;
      margin-block-end: rem(30);
    }
  }


  &__submit {
    margin-block-start: rem(50);
  }

  &__close-pic {
    padding: 0;
    fill: $white;
    width: rem(35);
    height: auto;

    @include tablet {
      width: rem(40);
      height: auto;
      fill: rgba(#000000, 0.4);
    }
  }

  &__close {
    display: flex;
    justify-content: center;
    align-items: center;
    width: rem(40);
    height: rem(40);
    border: none;
    color: $white;
    background: transparent;
    position: absolute;
    border-radius: 50%;
    top: -60px;
    right: -10px;
    transition-property: transform;
    transition-duration: 400ms;

    @include tablet {
      top: 20px;
      right: 20px;
    }

    &:hover {
      // background-color: rgba(#000000, 0.2);

      @include tablet {
        transform: rotate(120deg);
        transition-property: transform;
        transition-duration: 400ms;
      }
    }

    &:active {
      background-color: rgba(#000000, 0.3);

      @include tablet {
        background-color: rgba(#000000, 0.1);
      }
    }
  }
}
