.contacts {
  // overflow-x: hidden;

  &__form {
    @include section-wrapper;
    margin-block-end: rem(20);

    @include tablet {
      margin-block-end: rem(40);
    }
  }

  &__heading {
    @include heading-wrapper;
    margin-block-end: rem(20);

    @include tablet {
      margin-block-end: rem(40);
    }
  }

  &__desc {
    @include section-wrapper;
    text-align: center;
    margin-block-end: rem(20);
    color: $disabled;
    font-size: rem(14);
    max-width: rem(590);

    @include tablet {
      margin-block-end: rem(60);
      font-size: initial;
    }
  }

  &__info {
    @include section-wrapper;
    text-align: center;
    margin-block-end: rem(30);
    color: $disabled;
    font-size: rem(13);

    @include tablet {
      // margin-block-end: rem(50);
      font-size: rem(15);
    }
  }

  &__map {}
}
