.buttons-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: rem(20);
  max-width: rem(460);
  padding-inline: rem(27);

  @include tablet {
    justify-content: center;
  }

  &__wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: rem(15);
  }

  &__item {
    border: none;
    border-radius: 4px;
    background-color: $logo-yellow;
    width: rem(8);
    height: rem(8);
    flex-grow: 0;
    flex-shrink: 1;
    padding: 0;
    transition-property: width;
    transition-duration: 600ms;

    @include tablet {
      cursor: pointer;
      background-color: transparent;
      border: 2px solid $logo-yellow;
      border-radius: 8px;
      width: rem(15);
      height: rem(15);
    }

    &:hover {
      @include tablet {
        transition-property: transform;
        transition-duration: 30ms;
        opacity: 0.6;
        transform: scale(120%);
      }
    }

    &:active {
      @include tablet {
        opacity: 0.3;
      }
    }
  }


  &__item--current {
    background-color: $pink;
    width: rem(30);

    @include tablet {
      cursor: initial;
      border: none;
      width: rem(40);
      height: rem(10);
    }

    &:hover {
      @include tablet {
        opacity: 1;
        transform: scale(100%);
      }
    }

    &:active {
      @include tablet {
        opacity: 1;
      }
    }
  }


  &__slide {
    display: flex;
    justify-content: center;
    align-items: center;
    // text-align: center;
    width: rem(40);
    height: rem(40);
    background-color: $white;
    flex-shrink: 0;
    border-radius: 50%;
    font-size: rem(25);
    color: $disabled;

    @include tablet {
      display: none;
    }

    &:hover {
      opacity: 0.7;
    }

    &--hide {
      display: none;
    }
  }

  &__slide--right {}
}
